<template>
  <div>
    <p>这是一个插槽</p>
    <slot name="one" :us="user">{{user.id}}</slot>
    <slot name="two"></slot>
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: "sl",
  data() {
    return {
      user: {
        id: 1,
        name: "Li"
      }
    };
  }
};
</script>